footer {
  background: #000e;
  border-top: 4px solid #444;
  padding: 32px 0 112px;
}

footer > * {
  color: #f2f5deaa !important;
}

.footerWrapper {
  flex-direction: column;
  justify-content: center;
  font-size: larger;
  display: flex;
}

.contact {
  font-style: normal;
}

.footerWrapper small {
  opacity: .6;
}

.footerCol {
  flex-direction: column;
  flex: 1;
  padding: 24px;
  display: flex;
}

.footerCol.left {
  text-align: right;
}

.footerLogo {
  align-self: flex-end;
  width: min-content;
  height: 172px;
  padding: 24px 24px 0;
}

@media screen and (width >= 768px) {
  footer {
    padding: 56px 0 32px;
  }

  .footerWrapper {
    flex-direction: row;
    max-width: 1488px;
    margin: 0 auto;
  }

  .footerLogo {
    padding: 0 64px 24px;
  }
}

